
import Search from 'vux/src/components/search/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'

import InfiniteLoading from 'vue-infinite-loading';
import api from '../../assets/js/api'
export default {
components: {
Search,
Tab,
TabItem,
XImg,
InlineXNumber,
InfiniteLoading
},
data () {
return {
search: '',
onFetching: false,
bottomCount: 0,
changeValue: 1,
value: 1,
type: 'all',
types: ['all','discount','count'],
all: {
page: 1,
data: [{
"good_name": "杜蕾斯",
"stock": 10,
"good_price": 88,
"rebate": 2,
"id": "1",
"good_photo": "https://ss3.baidu.com/-rVXeDTa2gU2pMbgoY3K/it/u=1415756910,4153057836&fm=202&mola=new&crop=v1"
}]
},
discount: {
page: 1,
data: [{
"good_name": "杜蕾斯",
"stock": 10,
"good_price": 288,
"rebate": 12,
"id": "1",
"good_photo": "https://ss3.baidu.com/-rVXeDTa2gU2pMbgoY3K/it/u=1415756910,4153057836&fm=202&mola=new&crop=v1"
}]
},
count: {
page: 1,
data: [{
"good_name": "杜蕾斯",
"stock": 10,
"good_price": 188,
"rebate": 5,
"id": "1",
"good_photo": "https://ss3.baidu.com/-rVXeDTa2gU2pMbgoY3K/it/u=1415756910,4153057836&fm=202&mola=new&crop=v1"
}]
},
shoppingTrolley: 0,
}
},
computed:{
goods(){
return this[this.type].data;
},
total(){
let allMoney = 0,
discountMoney  = 0,
countMoney = 0,
allReMoney = 0,
discountReMoney  = 0,
countReMoney = 0,
allNumber = 0,
discountNumber = 0,
countNumber =0;
this.all.data.forEach( val => {
if( val.number > 0 ){
allMoney += val.good_price * val.number;
allReMoney += val.rebate * val.number;
allNumber += val.number;
}
} )
this.discount.data.forEach( val => {
if( val.number > 0 ){
discountMoney += val.good_price * val.number
discountReMoney += val.rebate * val.number
discountNumber += val.number;
}
} )
this.count.data.forEach( val => {
if( val.number > 0 ){
countMoney += val.good_price * val.number
countReMoney += val.rebate * val.number
countNumber += val.number;
}
} )
return {
totalMoney: (allMoney + discountMoney + countMoney) || 0,
totalNumber: (allNumber + discountNumber + countNumber) || 0,
totalReMoney: (allReMoney + discountReMoney + countReMoney) || 0
};
},
totalRebate(){

}
},
methods:{
onSubmit () {

this.$refs.search.setBlur()
this.resetData();
},
resetData(){
this.all.page = 1;
this.all.data = [];
this.discount.page = 1;
this.discount.data = [];
this.count.page = 1;
this.count.data = [];
this.$nextTick( () => {
this.$refs.infiniteLoading.$emit('$InfiniteLoading:reset');
} )
},
getResult(){
this.resetData();
},
getGoods ( $state ) {

this.$http.post( api.getGoods,{
type: this.type,
page: this[this.type].page,
name: this.search
} ).then( res => {
if( res.data[this.type].length ){
res.data[this.type].forEach( val => {
val.number = 0
} )
this[this.type].data = this[this.type].data.concat(res.data[this.type]);
this[this.type].page++;
this.shoppingTrolley = res.data.shoppingTrolley;
$state.loaded();
}else{
$state.complete();
}
} ).catch( e => {
$state.complete();
} )
},
change( data ){
console.log(data)
},
onItemClick( index ){
this.type = this.types[index]
this.$nextTick( () => {
this.$refs.infiniteLoading.$emit('$InfiniteLoading:reset');
} )
},
gotoShopCart(){
this.$router.push({
path: '/shopCart'
})
}
},
mounted(){

}
}



