
import XImg from 'vux/src/components/x-img/index.vue'
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

import InfiniteLoading from 'vue-infinite-loading';
import api from '../../assets/js/api'
export default {
components: {
XImg,
InlineXNumber,
CheckIcon,
InfiniteLoading
},
data () {
return {
bottomCount: 0,
changeValue: 1,
value: 1,
demo1: true,
isEdit: false,          // 是否处于编辑状态
goods: [{
"good_name": "杜蕾斯",
"stock": 10,
"good_price": 188,
"rebate": 5,
"id": "1",
"checked": true,
"amount": 12,
"good_photo": "https://ss3.baidu.com/-rVXeDTa2gU2pMbgoY3K/it/u=1415756910,4153057836&fm=202&mola=new&crop=v1"
}],
count: 0,
page: 1,
}
},
computed: {
total(){
let allMoney = 0,
allReMoney = 0,
allNumber = 0;
this.goods.forEach(val => {
if (val.amount > 0 && val.checked) {
allMoney += val.good_price * val.amount;
allReMoney += val.rebate * val.amount;
allNumber += val.amount;
}
})
return {
totalMoney: allMoney || 0,
totalNumber: (allNumber + this.count) || 0,
totalReMoney: allReMoney || 0
};
},
checkedGood(){
let deleteArr = [],
editArr = [];
this.goods.forEach( val => {
if( val.checked ){
deleteArr.push({
good_id: val.good_id
});
editArr.push({
good_id: val.good_id,
amount: val.amount
});
}
});
return {
deleteArr,
editArr
}
}
},
watch:{
demo1(curval){
this.goods.forEach( val => {
val.checked =  curval
});
}
},
methods:{
layer( text ){
this.$vux.toast.text( text || 'hello', 'middle')
},
showLoading(text){
this.$vux.loading.show({
text: text || '加载中'
})
},
hideLoading(){
this.$vux.loading.hide()
},
getGoods ( $state ) {
this.$http.post(api.shoppingTrolley)
.then( res => {
this.count = res.count
if( res.list.length ){
res.list.forEach( val => {
val.checked = true
})
this.page++
this.goods = this.goods.concat(res.list)
$state.loaded();
}else{
$state.complete();
}
}).catch( e => {
$state.complete();
})
},
submitOrder(){
this.$router.push({
path: '/pay'
})
},
deleteGoods(){
this.showLoading('删除中')
this.$http.post(api.shoppingTrolleyEdit,{
type: 'delete',
good: this.checkedGood.deleteArr
}).then( res => {

this.hideLoading();
this.layer('删除成功')
} ).catch( e => {
this.hideLoading();
} )
},
editGoods(){
this.showLoading('提交中')
this.$http.post(api.shoppingTrolleyEdit,{
type: 'modify',
good: this.checkedGood.editArr
}).then( res => {
this.isEdit = false;
this.hideLoading();
this.layer('编辑成功')
} ).catch( e => {
this.hideLoading();
} )
},
allChecked(){
this.goods.forEach( val => {
if( demo1 ){
val.checked = true;
}else{
val.checked = false;
}
} )
},
}
}



