
import XInput from 'vux/src/components/x-input/index.vue'

export default {
components: {
XInput
},
data () {
return {
isShowDesc: false,
isShowZh: false,
}
},
methods:{
layer( text ){
this.$vux.toast.text( text || 'hello', 'middle')
},
showLoading(){
this.$vux.loading.show({
text: '加载中'
})
},
hideLoading(){
this.$vux.loading.hide()
},
gotoUpload(){
this.$router.push({
path: '/upload'
})
}
},
mounted() {

}
}



