
import CheckIcon from 'vux/src/components/check-icon/index.vue'

export default {
components: {
CheckIcon
},
data () {
return {
demo1: false,
bottomCount: 2,
isEdit: false
}
},
methods:{
deleteAddress(){
const _this = this;
this.$vux.confirm.show({

content: '删除后不可恢复，确认删除？',

onCancel () {
console.log(this) 
console.log(_this) 
},
onConfirm () {}
})
},
setMainAddress(){
const _this = this;
this.$vux.confirm.show({

content: '确定设置为默认地址？',

onCancel () {
console.log(this) 
console.log(_this) 
},
onConfirm () {}
})
},
gotoAddAddress(){
this.$router.push({
path: '/addAddress'
})
}
}
}



