import XInput from 'vux/src/components/x-input/index.vue'
import value2name from 'vux/src/filters/value2name.js'

export default {
components: {
XAddress,
XInput,
Group
},

data () {
return {
demo1: true,
bottomCount: 2,
addressData: ChinaAddressV4Data,
showAddress: false,
tjname: '',
value: ['天津市', '市辖区', '和平区']
}
},
computed:{
address() {
return value2name(this.value, ChinaAddressV4Data)
},
},
methods:{

}
}



